@import url('https://fonts.googleapis.com/css?family=Muli&display=swap');

.body {
    background-color: steelblue;
    color: #fff;
    font-family: 'Muli', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    overflow: hidden;
    margin: 0;
}

.container {
    background-color: rgba(0, 0, 0, 0.4);
    padding: 41px 40px;
    border-radius: 5px;

    h1 {
        text-align: center;
        margin-bottom: 30px;
    }

    a {
        text-decoration: none;
        color: lightblue;
    }
}

.btn {
    cursor: pointer;
    display: inline-block;
    width: 100%;
    background: lightblue;
    padding: 15px;
    font-family: inherit;
    font-size: 16px;
    border: 0;
    border-radius: 5px;
}

.btn:focus {
    outline: 0;
}

.btn:active {
    transform: scale(0.98);
}

.text {
    margin-top: 30px;
}

.formControl {
    position: relative;
    margin: 20px 0 40px;
    width: 300px;

    input {
        background-color: transparent;
        border: 0;
        border-bottom: 2px #fff solid;
        display: block;
        width: 100%;
        padding: 15px 0;
        font-size: 18px;
        color: #fff;
    }

    input:focus,
    input:valid {
        outline: 0;
        border-bottom-color: lightblue;
    }

    input:focus+label,
    input:valid+label {
        span {
            color: lightblue;
            transform: translateY(-30px);
        }
    }

    label {
        position: absolute;
        top: 15px;
        left: 0;
        pointer-events: none;

        span {
            display: inline-block;
            font-size: 18px;
            min-width: 5px;
            transition: 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
        }
    }
}